<template>
    <section>
        <header>
          <h1>CONTACT LIST</h1>
        </header>
      <form>
        <new-friend
        @newFriendCreated="addNewFriend" 
        />
      </form>
          
        <ul>
            <friend-contact 
            @favoriteStatus="updateFavoriteStatus"
            @deleteFriend="deleteFriend"
            v-for= "friend in friends"
            :key= "friend.id"
            :id= "friend.id"
            :name= "friend.name" 
            :phone-number= "friend.phone"
            :email-adress= "friend.email"
            :favorite= "friend.favorite"
            
            />
            
        </ul>
    </section>
</template>

<script>
import FriendContact from './components/FriendContact.vue'
import NewFriend from './components/NewFriend.vue'
export default {
  components: {
     FriendContact,
     NewFriend
     },
    data(){
        return {
            friends:[ 
            {
                id: 'manuel',
                name: 'Manuel Lorenz',
                phone: '0123 45678 90',
                email: 'manuel@localhost.com',
                favorite: false
            },
            {
                id: 'julie',
                name: 'Julie Jones',
                phone: '0987 6541 21',
                email: 'julie@localhost.com',
                favorite: false
            }
            ]
        }
    },
    methods:{
      addNewFriend(name, phone, email){
        const newFriend = {
          id: name,
          name: name,
          phone: phone,
          email: email
        }
        this.friends.push(newFriend)
        
      },
      updateFavoriteStatus(id){
        
        //If the method "find" finds an item in the array with the same id => change the favorite to !favorite
        this.friends.find(e => e.id == id? e.favorite = !e.favorite: "")



        // e.favorite = !e.favorite
        // console.log(e);
        // this.favorite = !this.favorite
        // console.log(this.favorite)
        // alert(find.id)
      },
      deleteFriend(id){
        this.friends = this.friends.filter( element => element.id != id)
      }

    }
}

</script>



