<template>
    <li>
        <h2>{{name}} {{favorite? 'favorite' : ''}} </h2>
        <button @click="toggleDetails" > {{detailsAreVisible? 'Hide' : 'Show'}} details </button>
        <button @click="favoriteStatus">Mark as favorite</button>
        <button @click="deleteFriend" >Delete contact</button>
        <ul v-if="detailsAreVisible" >
            <li>
                <strong>Phone:</strong>
                {{phoneNumber}}
            </li>
            <li>
                <strong>Email:</strong>
                {{emailAdress}}
            </li>
        </ul>
    </li>
</template>

<script>
export default {
    props:[
        'id',
        'name',
        'phoneNumber',
        'emailAdress',
        'favorite'
    ],
    data(){
        return {
            detailsAreVisible: false,
            
        }
            
    },
    methods: {
        toggleDetails(){
            this.detailsAreVisible = !this.detailsAreVisible
        },

        favoriteStatus(){
            this.$emit('favoriteStatus', this.id)
        },
            
        deleteFriend(){
            this.$emit('deleteFriend', this.id)
        }
            
    }
}
</script>