<template>
    <div>
        <div>
            <label for="name">Name:</label>
            <input id="name" v-model="newName" type="text">
        </div>
        <div>
            <label for="phone">Telephone:</label>
            <input id="phone" v-model="newPhone" type="tel">
        </div>
        <div>
            <label for="email">Email:</label>
            <input id="email" v-model="newEmail" type="email">
        </div>
        <button @submit.prevent @click="createFriend" >Create new contact</button>
    </div>
</template>

<script>
export default {
    
    data(){
        return{
           newName:"",
           newPhone:"",
           newEmail:""
        }
    },
    methods:{
        createFriend(){
            this.$emit('newFriendCreated', this.newName, this.newPhone, this.newEmail)
            // this.newName=""
            // this.newPhone=""
            // this.newEmail=""
            
        }
        
    }
}
</script>